@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  background-color: #3f3f3f;
}
.container{
  width: 60%;
  background-color : rgb(57, 51, 51);
  border-radius: 10px;
  margin: auto;
  padding: 2%;
  /* transition: all 5s ease-out; */
}
.p2{
  background-color:rgb(106, 155, 201);
  margin-bottom: 2%;
  margin-top: 2%;
}

.expense-item{
    align-items: center;
    background-color: #4b4b4b;
    padding: 10px;
    border-radius: 5px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
    margin-bottom: 18px;
    display: flex;
    justify-content: space-between;
}
.Description{
    display: flex;
}
.Description div{
    margin-right: 10px;
}
.Amount{
    margin-right: 2%;
}
.Title{
    /* font-weight: bold; */
    color: white;
    align-items: center;
    padding-top: 10%;
}
.Date{
    width: 120px;
    /* font-weight: 900; */
    background-color: rgb(25, 25, 25);
    border-radius: 5px;
    border: 2px solid white;
    text-align: center;
    padding: 8px;
    color: white;
}
.Amount{
    padding: 10px;
    min-width: 120px;
    text-align: center;
    background-color: #40005d;
    border-radius: 5px;
    border: 2px solid white;
    color: white;
    font-size: 1.3rem;
    /* font-weight: bold; */
}
.Amount:hover{
    background-color: blueviolet;
}
.Date div{
    margin: 0;
}
.fck{
    display: flex;
}
#input-field{
    min-height: 100px;
}
form{
    display: flex;
    justify-content: space-between;
    position: relative;
    flex-wrap: wrap;
}
.Fields{
    margin-bottom: 3%;

}
.Fields label{
    display: block;
    font-size: xx-large;
    font-weight: bold;
    margin-bottom: 5%;
}
.Fields input{
    width: 400px;
    height: 50px;
    border-radius: 10px;
    /* box-shadow: 0px 10px 10pxblueviolet; */
}
.Fields button{
    margin-top: 40%;

    padding: 20px 50px;
    font-size: larger;
    border: 2px solid black;
}
.DropBox{
    margin-bottom: 5%;
}
.Chart{
    background-color: purple;
    height: 300px;
    border-radius: 10px;
    display: flex;
    flex-wrap: wrap;
    padding: 5%;
    justify-content: space-around;
}
.ChartBar_fill{
    border-radius: 10px;
    margin: auto;
    height: 100%;
    width: 12px;
    background-color: pink;
    overflow: hidden;
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
    
}
.Filled{
    height: 90%;
    background-color: red;
    /* border-radius: 10px; */
    transition: all 0.4s ease-out; 
    
}
