.expense-item{
    align-items: center;
    background-color: #4b4b4b;
    padding: 10px;
    border-radius: 5px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
    margin-bottom: 18px;
    display: flex;
    justify-content: space-between;
}
.Description{
    display: flex;
}
.Description div{
    margin-right: 10px;
}
.Amount{
    margin-right: 2%;
}
.Title{
    /* font-weight: bold; */
    color: white;
    align-items: center;
    padding-top: 10%;
}
.Date{
    width: 120px;
    /* font-weight: 900; */
    background-color: rgb(25, 25, 25);
    border-radius: 5px;
    border: 2px solid white;
    text-align: center;
    padding: 8px;
    color: white;
}
.Amount{
    padding: 10px;
    min-width: 120px;
    text-align: center;
    background-color: #40005d;
    border-radius: 5px;
    border: 2px solid white;
    color: white;
    font-size: 1.3rem;
    /* font-weight: bold; */
}
.Amount:hover{
    background-color: blueviolet;
}
.Date div{
    margin: 0;
}
.fck{
    display: flex;
}