.Chart{
    background-color: purple;
    height: 300px;
    border-radius: 10px;
    display: flex;
    flex-wrap: wrap;
    padding: 5%;
    justify-content: space-around;
}
.ChartBar_fill{
    border-radius: 10px;
    margin: auto;
    height: 100%;
    width: 12px;
    background-color: pink;
    overflow: hidden;
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
    
}
.Filled{
    height: 90%;
    background-color: red;
    /* border-radius: 10px; */
    transition: all 0.4s ease-out; 
    
}